.overlay-schedule {
  position: fixed;
  top: 118px;
  left: 0;
  z-index: 50;

  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.8);

  @media (max-width: $tablet-width) {
    top: 0;
  }
}

.overlay-schedule--sitemenu {
  @media (max-width: $tablet-width) {
    top: 118px;
  }
}
