.wrapper-cinema {
  background-image: url("../img/background-purple-desktop2.svg"), url("../img/background-purple-desktop2.svg");
  background-repeat: repeat-x;
  background-position-y: 240px, 240px;
  background-position-x: 0.2px;

  @media (max-width: $tablet-width) {
    background-position-y: 455px;
  }

  @media (max-width: $mobile-width) {
    background-image: url("../img/drw.svg"), url("../img/drw.svg");
    background-position-y: 252px, 252px;
  }
}
